import React, { useState } from "react";
import { Divider } from "@material-ui/core";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { Redirect } from "react-router";

import Rating from "@material-ui/lab/Rating";
import StarBorderIcon from "@material-ui/icons/StarBorder";

export default function HomepageProductGrid(props) {
  const [redirectToDetailPage, setredirectToDetailPage] = useState(false);

  if (redirectToDetailPage) {
    return <Redirect push to="/detailPage" />;
  }
  return props.products.map((eachItem, index) => (
    <React.Fragment key={eachItem.brand + index}>
      <Grid item style={{ padding: "1.2rem 0" }}>
        <Paper
          onClick={event => {
            setredirectToDetailPage(true);
          }}
          elevation={0}
          style={{
            borderRadius: "0",
            // border: "1px solid red",
            height: "20.5em",
            width: "15em",
            padding: "0 1em"
            // backgroundColor: "#f7f7f7"
          }}>
          <Grid
            // item
            container
            justify="center"
            alignItems="center"
            style={{
              padding: "1.2rem 0.5em"
            }}>
            <Grid item style={{ margin: "1em 1.5em 2.5em 1.5em" }}>
              <img
                alt=""
                src={eachItem.icon}
                style={{ width: "90%", height: "auto" }}
              />
            </Grid>

            <Grid item xs style={{ flexShrink: 1, display: "contents" }}>
              <Typography
                variant="body2"
                noWrap={false}
                style={{
                  fontWeight: 600,
                  height: "100%",
                  fontSize: "1em",
                  textAlign: "center",
                  display: "-webkit-box",
                  WebkitLineClamp: "2",
                  WebkitBoxOrient: "vertical",
                  overflow: "hidden",
                  textOverflow: "ellipsis"
                }}
                gutterBottom={false}>
                {eachItem.title}
              </Typography>
            </Grid>
            <Grid item style={{ paddingBottom: "0.5em" }}>
              <Typography variant="subtitle2">by {eachItem.brand},</Typography>
            </Grid>
            <Grid item>
              <Rating
                name="size-small"
                size="small"
                defaultValue={3.5}
                precision={0.5}
                emptyIcon={<StarBorderIcon fontSize="inherit" />}
                readOnly
                style={{
                  width: "10em",
                  justifyContent: "center"
                }}
              />
            </Grid>
            <Grid container justify="center" alignItems="center">
              <Grid item>
                <Typography
                  variant="body1"
                  //   display="block"
                  color="primary"
                  style={{ fontWeight: "bold" }}>
                  â‚¹12,999
                </Typography>
              </Grid>
              <Grid item>
                <Typography
                  //   variant="body2"
                  color="secondary"
                  style={{
                    color: "grey",
                    textDecorationLine: "line-through",
                    marginLeft: "0.8em",
                    fontSize: "14px"
                    // display: "inline"
                  }}>
                  â‚¹13,999
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      {props.products.length - 1 !== index ? (
        <Grid>
          <Divider
            id="divider"
            orientation="vertical"
            style={{ margin: "15px 0", height: "20em" }}
            light
          />
        </Grid>
      ) : (
        undefined
      )}
    </React.Fragment>
  ));
}
